"use client";
import React, { useState } from 'react';
import Link from 'next/link';
import Button from './Button';

export default function Layout({ children }: { children: React.ReactNode }) {
  const [open, setOpen] = useState(false);

  return (
    <div className="min-h-screen flex flex-col bg-gray-50">
      <header className="bg-white border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center py-4">
            <div className="flex items-center gap-4">
              <Link href="/" className="text-2xl font-bold text-blue-600">Clinics</Link>
              <p className="text-sm text-gray-500 hidden sm:block">Book appointments quickly</p>
            </div>

            <nav className="hidden md:flex items-center gap-4">
              <Link href="/clinics" className="text-gray-700 hover:text-gray-900">Clinics</Link>
              <Link href="/doctors" className="text-gray-700 hover:text-gray-900">Doctors</Link>
              <Link href="/about" className="text-gray-700 hover:text-gray-900">About</Link>
              <Link href="/login">
                <Button variant="outline" size="sm">Login</Button>
              </Link>
            </nav>

            <div className="md:hidden flex items-center">
              <button
                aria-label="Toggle menu"
                onClick={() => setOpen(!open)}
                className="p-2 rounded-md hover:bg-gray-100"
              >
                <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 text-gray-700" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d={open ? 'M6 18L18 6M6 6l12 12' : 'M4 6h16M4 12h16M4 18h16'} />
                </svg>
              </button>
            </div>
          </div>

          {open && (
            <div className="md:hidden py-2">
              <div className="flex flex-col gap-2">
                <Link href="/clinics" className="px-2 py-2 rounded hover:bg-gray-100">Clinics</Link>
                <Link href="/doctors" className="px-2 py-2 rounded hover:bg-gray-100">Doctors</Link>
                <Link href="/about" className="px-2 py-2 rounded hover:bg-gray-100">About</Link>
                <Link href="/login" className="px-2 py-2 rounded hover:bg-gray-100">Login</Link>
              </div>
            </div>
          )}
        </div>
      </header>

      <main className="flex-1">
        {children}
      </main>

      <footer className="bg-white border-t mt-8">
        <div className="max-w-7xl mx-auto px-4 py-6 text-sm text-gray-600">Â© 2025 Clinics Booking System</div>
      </footer>
    </div>
  );
}
